import styled, { css } from "styled-components";
import AnimatedNumber from "animated-number-react";
import Twemoji from 'react-twemoji';
import { useInterval } from "./hooks/useInterval";
import { emojiData } from "./data/dataForHeader";
import { examples } from "./data/examples";
import { useState } from "react";
import { Theme, lightTheme } from "./theme/ThemeProvider";

const Header = styled.header(props=>css`
  display: flex;
  flex-flow: column;
  gap: 1.5rem;
  width: 100vw;
  justify-content:center;
  align-items: center;
  margin-bottom: 1rem;
`)

const Topline = styled.div(props=>css`
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  height: 6rem;
  gap: 1rem;
`)

const StyledNumber = styled(AnimatedNumber)(props=>css`
  font-weight: 700;
  width: 6ch;
  color: ${props => props.value > 0 ? props.theme.happy  : props.theme.sad};
`)

const StyledTwemoji = styled(Twemoji)(props=>css`
  width: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
`)

const StyledMain = styled.main(props=>css`
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: flex-start;
  width: 100vw;
  gap: 2.5rem;
`)

const StyledSection = styled.section(props=>css`
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
`)

const Paragraph = styled.p(props=>css`
  width: 65ch;
  max-width: 95%;
  text-align: ${props.align};
  line-height: 150%;
`)

const NavigationLinks = styled.ul(props=>css`
  list-style: none;
  display: flex; 
  flex-flow: row wrap;
  justify-content: space-evenly;
  width: 65ch;
  max-width: 95%;
  padding: 0;
  margin: 0;
`)

const NavigationItem = styled.li(props=>css`
  list-style: none;
  padding: 0;
  margin:0 ;
`)

const Link = styled.a(props=>css`
  color: ${props=>props.theme.interactive};
  font-weight: 700;
  text-decoration: none;
  &:visited {
    color: ${props=>props.theme.interactive};
  }
`)

const ExampleList = styled.dl(props=>css`
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-flow: column;
  gap: 1.5rem;
`)

const ExampleType = styled.dt(props=>css`
    background: ${props=>props.theme.accent};
    color: ${props=>props.theme.background};
    width: fit-content;
    display: inline;
    padding: 0.0625rem 0.125rem;
`)

const ExampleUrl = styled.dd(props=>css`
  font-family: 'Fira Code';
  display: inline;
  margin-left: 0.25rem;
`)

const ExampleResponseLabel = styled.dt(props=>css`
  font-family: Bree Serif;
`)

const ExampleResponseBody = styled.dd(props=>css`
  margin: 0;
`)

const Example = props=>(
  <section>
  <ExampleType>{ props.type }</ExampleType>
  <ExampleUrl>{ props.url }</ExampleUrl>
  <ExampleResponseLabel>Response</ExampleResponseLabel>
  <ExampleResponseBody>
    <pre>
    { JSON.stringify(props.response, null, 2) }
    </pre>
  </ExampleResponseBody>
  </section>
)

const randomValue = upperLimit => Math.floor(Math.random()*upperLimit)

function App() {
  const [randomEmoji, setRandomEmoji] = useState(randomValue(emojiData.length))
  useInterval(()=>{
    let newRandomEmoji = randomValue(emojiData.length);
    do {
      newRandomEmoji = randomValue(emojiData.length);
    } while (newRandomEmoji === randomEmoji);
    setRandomEmoji(newRandomEmoji)
  }, 3000)
  return (
    <Theme theme={lightTheme}>
      <Header>
        <Topline>
          <h1>emojinal</h1><StyledTwemoji aria-hidden="true">{emojiData[randomEmoji].char}</StyledTwemoji><StyledNumber aria-hidden="true" value={emojiData[randomEmoji].sentimentScore} formatValue={(value) => `${value > 0 ? '+' : ''}${value.toFixed(3)}`} />
        </Topline>
        <h2>An experimental API for determining emoji sentiment</h2>
      </Header>
      <StyledMain>
        <StyledSection>
          <h3>About</h3>
          <Paragraph align="center">In planning as series of Miro-based workshops, I had the idea that participants can express how they feel about different topics/statements/whatever using emojis. Whilst emoji communication is de rigueur in 2021, its meanings aren't strictly codified.</Paragraph>
          <Paragraph align="center">Luckily, the Slovenia's <Link href="https://ijs.si/ijsw">Institut "Jo≈æef Stefan"</Link> has performed a massive analysis of emoji usage in tweets, and documented their findings in a <Link href="https://doi.org/10.1371/journal.pone.0144296">research paper</Link>.</Paragraph> 
          <Paragraph>As a starting point, I'm calling upon <Link href="http://kt.ijs.si/data/Emoji_sentiment_ranking/">that data</Link> to perform some automated analysis of participant sentiment.</Paragraph>
          <NavigationLinks>
            <NavigationItem><Link href="mailto:key@emojin.al?subject=API key request">Request an API key</Link></NavigationItem>
            <NavigationItem><Link href="https://github.com/jrmedd/emojinal">Learn more on GitHub</Link></NavigationItem>
          </NavigationLinks>
        </StyledSection>
        <StyledSection>
          <h3>Examples</h3>
          <ExampleList>
            {
              examples.map(example=><Example {...example} />)
            }
          </ExampleList>
        </StyledSection>
      </StyledMain>
    </Theme>
  );
}

export default App;
